.insta{
  background: linear-gradient(to right, #240b36, #c31432);
  width: 180px;
  height: 40px;
  align-items: center;
  border: none;
  font-size: 1.3rem;
  text-decoration: none;
  

  color: rgb(39, 191, 211);
  cursor: pointer;
  border-radius: 0 25px;
  position: relative;
  transition: all .6s;
}
.insta:focus, .insta:hover, .insta:visited, .insta:link, .insta:active{
  text-decoration: none;
}
.insta:hover{
  border-radius: 25px 0;
}

.insta::before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(to right, #9225e0, #14afc3);
  width: 100%;
  height: 100%;
  filter: blur(22px);
  transform: scale(0.9, 0.8) translateY(20px);
  z-index: -1;
  opacity: 0;
  transition: all .6s;
}

.insta:hover::before{
  opacity: 1;
}


.media {
  position: absolute;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
  margin: 0;
  padding: 0;
  display: flex;


}

